html,
body {
  font-family: Arial, sans-serif;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

/* ---------- HEADER ---------- */
.App-logo {
  height: 1.5rem;
  padding-left: 1.25rem;
  z-index: 2000;
}

.App-header {
  z-index: 1000;
  background-color: #456173;
  /* padding: 20px; */
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 3rem;
}

.App-title {
  font-size: 1.5rem;
  margin-bottom: 0;
  color: #f2f2f0;
}

/* ---------- MAIN ---------- */
.container {
  height: 95vh;
  position: relative;
}

input {
  font-size: 12px;
}

h1 {
  color: #525454;
  font-size: 22px;
  margin: 0 0 10px 0;
  text-align: center;
}

hr {
  background: #D0D7D9;
  height: 1px;
  margin: 20px 0 20px 0;
  border: none;
}

/* ---------- GOOGLE MAP ---------- */
#map {
  top: 0;
  /* bottom:0px; */
  height: calc(100vh - 3rem);
  left: 260px;
  position: absolute;
  right: 0px;
}

.map-offline {
  z-index: 6777271;
  /* border: 2px black solid; */
  text-align: center;
  padding: 20%;
}

/* ---------- SIDE-BAR ---------- */
.side-menu {
  z-index: 500;
  background: #1b3c59;
  /* border-right: .25vh solid #999; */
  /* border-radius: 3px; */
  line-height: 35px;
  /* padding: .5vh; */
  text-align: left;
  width: 20vw;
  min-width: 270px;
  height: calc(100vh - 3rem);
  position: absolute;
  overflow-y: auto;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

/* filter search text box */
.search-input {
  width: 100%;
  height: 2.5rem;
  font-size: .75rem;
  padding-left: 10px;
  margin: 0;
}

/* List of resturants */
.side-list {
  list-style: none;
  padding-left: 0;
  /* padding: 1rem; */
  /* color: white; */
  width: 250px;
}

/* Restaurant item in list */
.side-item {
  background-color: #f2f2f0;
  padding: .5rem;
  margin: .5rem;
  margin-left: .25rem;
  margin-right: .25rem;
  height: auto;
  max-height: 125px;
  width: 225px;
  max-width: 241px;
  cursor: pointer;
  border-radius: .25rem;
}

.list-item-title {
  font-weight: bold;
  color: rgb(43,43,43);
  /* color: white; */
  background-color: #b8d8eb;
  /* background-color: rgb(108,191,80); */
  width: 221px;
  margin-top: -10px;
  /* padding-top: 10px; */
  padding: 5px 10px;
  margin-left: -8px;
  margin-bottom: 15px;
  border-radius: .25rem;

    -webkit-box-shadow: 0 5px 5px 0px rgba(46,61,73,0.6);

            box-shadow: 0 5px 5px 0px rgba(46,61,73,0.6);
    overflow: hidden;
    -webkit-transition: box-shadow 2s; /* Safari */
    -webkit-transition: -webkit-box-shadow .3s;
    transition: -webkit-box-shadow .3s;
    -o-transition: box-shadow .3s;
    transition: box-shadow .3s;
    transition: box-shadow .3s, -webkit-box-shadow .3s;

}

.fbinfo {
  height: 100px;
}

.fb-offline-warning {
  font-size: .75rem;
}

/* ---------- SCROLL BAR ---------- */
::-webkit-scrollbar {
    width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(69,97,115,0.5);
    border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: rgba(255,255,255,1);
    -webkit-box-shadow: inset 0 0 6px rgba(69,97,115,0.5);
}

/* ---------- FOOTER ---------- */
.footer {
  /* position: absolute; */
  /* bottom: 0px; */
  z-index: 15000;
  background-color: #456173;
  color: #f2f2f0;
  font-size: .75rem;
  height: 2rem;
  width: 270px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

/* ---------- ADJUST FOR SMALLER SCREENS ---------- */
@media screen and (max-width:500px) {

  .side-menu {
    left: -100%;
  }

  #map {
    left: 0;
  }
}



